.content{
  width: 1310px;
  display: flex;
}

.content__menu{
  position: absolute;
  display: none;
}

.content__menu_img{
  padding: 5px;
  width: 50px;
  height: 50px;
  transition: .3s;
  &:hover{
    transform: scale(1.1);
  }
}

@media(max-width: 768px){
  .content{
    max-width: 768px;
    width: 100%;
    width: fit-content;
  }
  .content__menu{
    display: block;
  }
}